@font-face {
    font-family: 'GothamLight';
    src: url('GothamLight.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'GothamMedium';
    src: url('GothamMedium.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'GothamBook';
    src: url('GothamBook.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'GothamBold';
    src: url('Gotham-Bold.otf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

body {
  font-family: 'GothamMedium';
  font-size: 14px;
  padding: 0; margin: 0;
  overflow: auto;
  background: var(--purple);
  height:10000px;
}

:root {
  --brown:  #54332E;
  --light-brown: #685454;
  --black: #0D080A;
  --dark-green: #2C4449;
  --grey: #9F9E9D;
  --white: #D8D6D4;
  --orange-brown: #9F5B3C;
  --greenish: #968C63;
  --purple: #190c1a;
}

.main-container {
  height:100%;
  width: 90%;
  margin:auto;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.main-header {
  
  background: url("https://www.transparenttextures.com/patterns/60-lines.png"), linear-gradient(-45deg, var(--light-brown), var(--brown), var(--dark-green), var(--greenish));
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  height: 700px; 
  display: flex;
  flex-direction: column;
  padding:2rem;
}
.main-header-topnav {
  display: flex;
  flex-direction: row;
  padding: 2rem 0;
  color:var(--grey);
}
.main-header-logo {
  font-size: 24px;
  font-family: 'GothamBold';
}
.main-header-navigation {
  margin-left: auto;
}
.main-header-nav {

}
.main-header-navigation a{
  text-decoration: none;
  padding-left: 1.75rem; padding-right: 1.75rem;
  color: var(--grey);
  font-family: 'GothamLight';
  letter-spacing: 2px;
  font-weight:600;
  font-size: 16px;
  transition: .3s;
}

.main-header-navigation a:hover{
  color: #FFF;
}

.main-header-headercontent {
  display:flex;
  flex-direction: row;
  height:100%;
  margin-top:20px
}
.main-header-headercontentleft {
 width:50%;
}
.main-header-headercontenth1 {
  font-family: 'GothamBold';
  color:#FFF;
  font-size:72px;
  width:750px
}
.main-header-headercontentleftcontent {
  color:var(--grey);
  font-size:18px;
  width:600px;
}

.main-header-headercontentleftbottom {
  display:flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top:50px;
  background: rgba(255,255,255,0.07);
  height:60px;
  font-size:15px;
  border: 3px solid rgba(255,255,255,0.1);
  border-radius: 34px 6px;
  font-family:'GothamBold';
  width:500px;
  transition:.3s;
}
.main-header-headercontentleftbottom-div {
  color:var(--grey);
  width:100%;
  height:30%;
  display:flex;
  align-items:center;
  justify-content: center;
  border-right: 2px solid var(--grey);
  transition:.3s;
  cursor:pointer;
}
.bottom-div-noborder {border:none;}
.bottom-div-last {
  border:none;
  background:#f0932b;
  height:100%;
  color:var(--brown);
  border-radius: 34px 6px;
}
.main-header-headercontentleftbottom-div:hover {
  color:#FFF;
}

.main-header-headercontentright {
  margin-left:auto;
  
 }
 .main-header-headercontentrightcontent {
   background: url('https://preview.redd.it/7oayrvrb85171.png?auto=webp&s=f357a038e33f301c98b38c378483d123e055c454') no-repeat center;
   background-size:cover;
   height:750px;width:500px;
   border-radius: 34px 6px;
   display:flex;
   justify-content: bottom;
 }
 .main-header-headercontentleftbottom:hover {
  transform:scale(1.01);
 }

 .main-header-headercontentrightcontent-credits {
   color:#FFF;
   margin-top: auto;
   padding:6px;
   margin-bottom:6px;
   margin-left:6px;
   border-radius: 14px;
   background:rgba(255,255,255,0.06);
   text-decoration:none;
 }
